@import "../../app.scss";

.navbar {
  width: 100%;
  color: white;
  font-size: 14px;
  position: fixed;
  top: 0;
  z-index: 999;
  background: linear-gradient(to top, transparent 0%, rgb(0, 0, 0, 0.3) 50%);

  &.scrolled {
    background-color: var(--main-color);
  }

  .container {
    padding: 0px 50px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 70px;

    .left {
      display: flex;
      align-items: center;

      img {
        height: 25px;
        margin-right: 40px;

        @include mobile {
          display: block;
          margin-left: -12%;
        }
      }

      span {
        margin-right: 25px;
        cursor: pointer;
        font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;

        @include tablet {
          display: none;
        }

        @include mobile{
          margin-left: -20%;
        }

        &.navbarmainLinks {
          @include tablet {
            display: block;
          }
        }
      }
      span:hover{
        color: yellow;
      }
    }

    .right {
      display: flex;
      align-items: center;

      .bellicon{
        color: rgb(163, 20, 20);
        box-shadow: 0 6px 4px -4px pink;
      }

      @include mobile{
        flex-direction: column !important;
        margin-left: -20% !important;
        margin-top: 10%;
      }

      .notif{
        display: flex;
        @include mobile{
          display: flex;
          margin-top: 5%;
          margin-left: -15%;
        }
      }

      .sicon{
        @include mobile{
          display: none;
        }
      }

      .icon {
        margin: 0px 15px;
        cursor: pointer;
        @include mobile{
          // display: none;
          margin: 0px 10px;
        }
      }

      .uName{
        @include mobile{
          margin-left: auto;
          margin-left: -25%;
          // background-color: aqua;
          width: 120px;
          text-align: center;
        }
      }

      span{
        @include mobile{
          margin-left: -35%;
        }
      }

      img {
        width: 30px;
        height: 30px;
        border-radius: 5px;
        object-fit: cover;
        cursor: pointer;
        @include mobile{
          // display: none;
        }
      }

      .profile {

        @include mobile{
          margin-top: 5%;
        }

        .options {
          display: none;
          background-color: var(--main-color);
          border-radius: 5px;

          @include mobile{
            width: 60px;
            padding: 3px;
            margin-left: -4% !important;
            text-align: center;
            padding-left: 5%;
          }
        }

        span {
          padding: 10px;
          cursor: pointer;
        }

        &:hover {
          .options {
            display: flex;
            flex-direction: column;
            position: absolute;
          }
        }
      }
    }
  }
}

