.login {
  width: 100vw;
  height: 100vh;
  background: linear-gradient(
      to bottom,
      rgba(0, 0, 0, 0.2) 0%,
      rgba(0, 0, 0, 1) 100%
    ),
    url("https://ceotudent.com/wp-content/uploads/2020/05/netflix-poster-1024x576.jpg");
  background-size: cover;
  position: relative;

  .top {
    .wrapper {
      padding: 20px 50px;
      display: flex;
      align-items: center;
      justify-content: space-between;
      background-color: rgb(0, 0, 0, 0.5);

      .logo {
        height: 40px;
      }
    }
  }

  .container {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    color: white;

    form {
      width: 300px;
      height: 300px;
      padding: 30px;
      border-radius: 5px;
      // background-color: var(--main-color);
      background-color: rgb(0, 0, 0, 0.8);
      display: flex;
      flex-direction: column;
      justify-content: space-around;

      h1{
        color: gainsboro;
      }

      input {
        height: 40px;
        border-radius: 5px;
        background-color: rgb(202, 202, 212);
        color: black;
        padding-left: 10px;
        font-size: 15px;
        font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;

        &::placeholder {
          color: gray;
        }
      }

      button {
        height: 40px;
        border-radius: 5px;
        background-color: red;
        color: gainsboro;
        border: none;
        font-size: 16px;
        font-weight: 500;
        cursor: pointer;
      }
      button:hover{
        box-shadow: 0 0 3px white;
      }

      span {
        color: lightgray;
        font-size: 16px;
        b {
          color: white;
          cursor: pointer;
        }
      }
    }
  }
}


@media screen and (max-width: 673px){
  form{
    width: 250px !important;
  }
}