@import "../../app.scss";

.featured {
  height: 76vh;
  position: relative;
  background-size: 100% 120% !important;

  .movie_info{
    position: absolute;
    height: 100%;
    width: 100vw;
    background-color: rgba(17, 17, 17, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;

    .movie_info_child{
      width: 30%;
      height: auto;
      background-color: rgb(234, 236, 92);
      border-radius: 8px;
      display: flex;
      flex-direction: column;
      border: solid 1px black;
      box-shadow: 0 0 5px white;

      @include mobile {
        width: 80%;
      }

      button{
        width: 15%;
        margin-left: auto;
        margin-right: 2%;
        margin-top: 1%;
        border-radius: 5px;
        cursor: pointer;
        font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
        font-weight: bold;
        background-color: lightgreen;

        @include mobile {
          width: 20%;
        }
      }
      button:hover{
        background-color: orange;
      }

      .movie_detail{
        margin-top: 1%;
        margin-bottom: 5%;
      }

      .info_data{
        margin-top: 3%;
        margin-left: 3%;
        color: rgb(30, 30, 31);
        font-family: cursive;
        display: flex;
        margin-right: 2%;

        .attr{
          font-weight: bold;
          font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
        }

        .value{
          margin-left: 2%;
          font-family: "cursive";
        }
      }
      }
      }
  
  

  .category {
    position: absolute;
    top: 80px;
    right: 0;
    margin-right: 7%;
    font-size: 30px;
    font-weight: 500;
    color: white;
    display: flex;
    align-items: center;

    @include mobile {
      left: 40px;
    }

    select {
      cursor: pointer;
      background-color: var(--main-color);
      border: 1px solid white;
      color: white;
      margin-left: 20px;
      padding: 5px;
    }
  }

  #genre{
    z-index: 2;
  }

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  .info {
    width: 35%;
    position: absolute;
    left: 50px;
    bottom: 60px;
    color: white;
    display: flex;
    flex-direction: column;

    .play:hover{
      box-shadow: 0 0 10px lightblue;
    }
    .more:hover{
      box-shadow: 0 0 10px lightblue;
    }

    .vid{
      border: solid 1px white;
      height: 40vh;
      background-color: rgb(0, 0, 0, 0.7);
      border-radius: 5px;
      margin-bottom: -4%;

      @include mobile {
        height: 20vh;
        width: 50vw;
      }
    }

    // video {
    //   width: 40vw;
    //   margin-top: 150px !important;
    // }

    .desc {
      margin: 20px 0px;
      font-family: cursive;
      color: rgb(240, 240, 106);

      @include mobile {
        width: 60vw;
      }
    }

    .buttons {
      display: flex;

      button {
        padding: 10px 20px;
        border: none;
        border-radius: 5px;
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 18px;
        font-weight: 500;
        margin-right: 10px;
        cursor: pointer;

        &.play {
          background-color: white;
          color: var(--main-color);
        }

        &.more {
          background-color: gray;
          color: white;
        }

        span {
          margin-left: 5px;
        }
      }
    }
  }
}
